@import "../../global.scss";

.portfolio {
  background-color: rgb(206, 248, 248);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;

  
  h1 {
    font-size: 50px;

    @include mobile{
      font-size: 20px;
    }
  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;

    @include mobile{
      margin: 10px 0;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    overflow:hidden;  
    @include mobile{
      width: 100%;
    }

    .item {
      height: 70vh;
      
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all .5s ease;
      cursor: pointer;

      @include mobile{
        width: 130px;
        height: 100px;
      }

      .text  {
        text-align: center;
        margin: 20px;
        position: absolute;
        font-size: 20px;
        align-items: center;
        justify-content: center;
      }

      h3, h4, h6 {
        margin:10px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
